.header {
  position: absolute;
  width: 100%;
  padding: 30px 0;

  @include vp-1199 {
    padding: 34px 0;
  }

  @include vp-767 {
    padding: 15px 0;
  }
}

.header__container {
  display: flex;
  justify-content: space-between;

  @include vp-1199 {
    align-items: center;
  }
}

.header__logo img {
  height: 44px;

  @include vp-1199 {
    width: 82px;
    height: 17px;
  }

  @include vp-767 {
    width: 69px;
    height: 24px;
  }
}

.header__phone {
  font-family: $ff-alumni-sans;
  font-style: normal;
  font-weight: 600;
  font-size: 26px;
  line-height: 31px;
  letter-spacing: 0.03em;
  color: $color-default-white;

  @include vp-1199 {
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.02em;
  }

  @include vp-767 {
    font-size: 20px;
    line-height: 24px;
  }
}
