.subscriptions {
  padding-top: 120px;
  padding-bottom: 100px;
  background-color: $color-ghost-white;
  position: relative;

  &::after {
    position: absolute;
    content: "";
    background-image: url("../img/weights-desktop.png");
    background-repeat: no-repeat;
    width: 287px;
    height: 415px;
    bottom: -50px;
    right: 0;

    @include vp-1199 {
      background-image: url("../img/weights-tablet.png");
      bottom: -95px;
      width: 347px;
    }

    @include vp-767 {
      display: none;
    }
  }

  @include vp-1199 {
    padding-top: 101px;
    padding-bottom: 70px;
  }

  @include vp-767 {
    padding-top: 65px;
    padding-bottom: 45px;
  }
}

.subscriptions__container {
  display: flex;
  flex-direction: column;
  align-items: center;

  @include vp-1199 {
    padding: 0 163px;
  }
}

.subscriptions h2 {
  font-family: $ff-alumni-sans;
  font-weight: 600;
  font-size: 54px;
  line-height: 65px;
  letter-spacing: 0.03em;
  color: $color-sapphire;
  margin-top: 0;
  margin-bottom: 8px;

  @include vp-1199 {
    font-size: 42px;
    line-height: 50px;
    margin-left: -14px;
    margin-bottom: 10px;
  }

  @include vp-767 {
    font-size: 32px;
    line-height: 38px;
    margin-left: -12px;
    margin-bottom: 10px;
  }
}

.subscriptions__tabs {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.subscriptions__buttons {
  display: grid;
  grid-template-columns: auto auto auto;
  column-gap: 46px;
  border-bottom: 2px solid rgba(120, 120, 120, 0.2);
  margin-bottom: 39px;
  width: fit-content;

  @include vp-1199 {
    column-gap: 54px;
    margin-bottom: 45px;
  }

  @include vp-767 {
    column-gap: 45px;
    margin-bottom: 35px;
  }
}

.subscriptions__buttons button {
  font-family: $ff-roboto-condensed;
  font-weight: 400;
  font-size: 20px;
  line-height: 150%;
  color: rgba(16, 38, 84, 0.5);
  padding: 10px 0;
  border: 0;
  background-color: transparent;

  @include vp-1199 {
    font-size: 18px;
  }

  @include vp-767 {
    font-size: 16px;
    padding: 9px 0 11px;
  }
}

.subscriptions__buttons .active-button {
  color: $color-sapphire;
  border-bottom: 2px solid #d10a42;
}

.subscriptions__list {
  grid-template-columns: 1fr 1fr 1fr;
  gap: 40px;
  padding: 0;
  margin: 0;
  list-style-type: none;
  margin-bottom: 30px;
  display: none;
  z-index: 1;

  @include vp-1199 {
    flex-direction: column;
    gap: 42px;
  }

  @include vp-767 {
    gap: 40px;
  }
}

.subscriptions__item {
  background-color: $color-default-white;
  padding: 60px 77px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 360px;

  @include vp-1199 {
    width: 100%;
    min-width: 442px;
    padding: 62px 118px 68px;
  }

  @include vp-767 {
    min-width: 290px;
    padding: 50px 42px;
  }
}

.subscriptions__item h3 {
  font-family: $ff-roboto-condensed;
  font-weight: 700;
  font-size: 26px;
  line-height: 29px;
  color: $color-sapphire;
  margin-top: 0;
  margin-bottom: 12px;

  @include vp-1199 {
    margin-bottom: 8px;
  }

  @include vp-767 {
    font-size: 22px;
    line-height: 110.16%;
  }
}

.subscriptions__item p {
  font-family: $ff-roboto-condensed;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: #495a7d;
  margin-top: 0;
  margin-bottom: 66px;

  @include vp-767 {
    margin-bottom: 55px;
  }
}

.subscriptions__price {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 33px;

  @include vp-767 {
    margin-bottom: 30px;
  }
}

.subscriptions__item span {
  font-family: $ff-roboto-condensed;
  font-style: normal;
  font-weight: 700;
  font-size: 60px;
  line-height: 150%;
  color: #103aae;
  position: relative;
  margin-left: 5px;

  @include vp-767 {
    font-size: 48px;
    margin-left: 7px;
  }

  &:last-of-type::after {
    content: "₽";
    color: $color-sapphire;
    margin-left: 14px;

    @include vp-767 {
      margin-left: 7px;
    }
  }

  &:first-of-type {
    position: absolute;
    font-style: normal;
    font-size: 100px;
    color: #e8e8e8;
    opacity: 0.48;
    top: -57px;

    @include vp-767 {
      font-size: 80px;
      top: -45px;
    }
  }
}

.subscriptions__item a {
  font-family: $ff-roboto-condensed;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $color-sapphire;
  padding: 24px;
  border: 2px solid $color-sapphire;
  box-sizing: border-box;
  width: 100%;
  text-align: center;
}

.subscriptions__tabs .current-tab {
  display: grid;

  @include vp-1199 {
    display: flex;
  }
}
